  institute_logo: any;
  readFile(file: any): any {
    console.log(file);
    const reader = new FileReader();
    if (file) {
      reader.readAsDataURL(file);
      reader.onloadend = () => {
        this.institute_logo = (<string>reader.result).split(',')[1];
        return (<string>reader.result).split(',')[1];
      };
    }
  }

